
.grid-container {
  display: grid;
  font-family: Libre Baskerville;
  grid-template-areas:
    'header header header header header header'
    'menu menu main main main main';
  gap: 30px;
  background-color: rgb(146, 146, 146);
  padding: 10px;
}

.grid-container > div {
  background-color: rgb(255, 255, 255);
  
  padding: 20px 20px;
  font-size: 20px;
}

.Filters {
  grid-area: menu;
}

.SearchBox {
  grid-area: header;
}

.SearchList{
  grid-area: main;
  text-align: center;
}

.HitImage {
  max-width: 100%;
  height: auto;
}